import { getActClass } from './utils';

const WX_JS_SDK = 'https://res.wx.qq.com/connect/zh_CN/htmledition/js/wxLogin.js';


export default {
  name: 'PressPCScanLogin',
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    url: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: '微信登录',
    },
    destroyedWhenClosed: {
      type: Boolean,
      default: false,
    },
    loader: {
      type: Function,
      default: null,
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    wxLoginConfig: {
      type: Object,
      default: () => ({}),
    },
  },
  emits: [
    'update:show',
  ],
  data() {
    return {
    };
  },
  watch: {
    show: {
      handler(value) {
        if (value) {
          this.init();
        }
      },
    },
    url: {
      handler() {
        this.init();
      },
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {

      if (typeof this.loader === 'function') {
        this.loader(WX_JS_SDK, () => {
          this.initQRCode();
        });
        return;
      }

      this.initQRCode();

    },
    initQRCode() {
      new window.WxLogin({
        id: 'login_container', // div的id
        self_redirect: false,
        appid: 'wx224053afb6d8a600',
        scope: 'snsapi_login',
        redirect_uri: this.url, // encodeURIComponent(this.redirectWxUrl),
        state: '7a6d6ed5589082dd6c41d64d5206b064',
        style: 'black', // 二维码黑白风格
        href: 'https://image-1251917893.file.myqcloud.com/igame-admin/weixinlogin.css',
        ...(this.wxLoginConfig || {}),
      });
    },
    closeDialog() {
      this.$emit('update:show', false);
      if (this.destroyedWhenClosed) {
        const dialogId = 'tip-dialog-login';
        const oldDialog = document.getElementById(dialogId);
        if (oldDialog?.parentElement) {
          oldDialog.parentElement.removeChild(oldDialog);
        }
      }
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
